.popup {
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    align-items: flex-start;
    justify-content: center;
    visibility: hidden;
    opacity: 1;
    transition: opacity .5s ease-in-out;
}
