.footer {
    max-width: 880px;
    margin: 0 auto;
    padding: 67px 0 60px;
}

@media screen and (max-width: 900px) {
    .footer {
        max-width: 700px;
    }
}

@media screen and (max-width: 780px) {
    .footer {
        max-width: 282px;
        padding: 48px 0 36px;
    }
}